<template>
  <!-- Popojok -->
  <section id="section-popojok" class="section-popojok" >
    <div class="container" >

      <div class="popojok-main">

          <div class="popojok-item" v-for="(item, index) in popojoks">
            <header class="item-header">
              <div class="header-author">
                <div class="author-avatar"><img style="width: 100%;height: 100%;" src="../assets/img/pic.png" alt=""></div>
                <div class="author-name">POPO</div>
                <div class="author-time">{{ item.created_at.human_time }}</div>
              </div>
              <div class="header-favorites">
                <div class="favorites-count">{{ item.likes }}</div>
              </div>
            </header>

              <div class="item-content type-1" v-if="[1,2,3,6].indexOf(item.type) != -1" :id="index">
                <img :src="item.attachment" :alt="item.title">
              </div>

              <div class="item-content type-4" v-if="item.type == 11" :id="index" >
                <popojok-quote :text="item.description"></popojok-quote>
              </div>

              <div class="item-content type-3 video" v-if="item.type == 5" :id="index">
                <popojok-video :cover="item.attachment_cover" :src="item.attachment" :id="item.id.toString()"></popojok-video>
              </div>

              <div v-if="item.type == 4" :id="index">
                <popojok-audio :duration="item.duration" :src="item.attachment" :id="item.id.toString()"></popojok-audio>
              </div>

              <!-- <div class="item-content type-4" v-if="index == 6">
                <p>Disini text aja.</p>
                <p>Disini text aja.</p>
              </div> -->

            <footer class="item-footer">
             

              
              <div  v-if="item.type == 1"> <b>POPO</b> Listening to <b>{{ item.title }}</b> by <b>{{ item.description }}</b> </div>
              <div  v-if="item.type == 2"> <b>POPO</b> watching <b>{{ item.title }}</b></div>
              <div  v-if="item.type == 3"> <b>POPO</b> reading <b>{{ item.title }}</b> by <b>{{ item.by }}</b> </div>
              <div  v-if="item.type == 4"> {{ item.title }} </div>
              <div  v-if="item.type == 5"> 
                <p>{{ item.title }}</p>
                <p>{{ item.description }}</p>
              </div>
              <div  v-if="item.type == 6">
                 <p>{{ item.title }}</p>
                 <p>{{ item.description }}</p>
              </div>
              <div  v-if="item.type == 11">
                {{ item.title }}
              </div> 
              
            </footer>
          </div>

        </div>

         <div
            v-infinite-scroll="loadMore"
            infinite-scroll-disabled="busy"
            infinite-scroll-distance="10"
            infinite-scroll-immediate-check="true"
          >
          </div>

        <div style="margin: -40px 0 40px 0;" class="spinner" v-show="loading"><img src="../../assets/img/spinner.svg" alt=""></div>
    </div>
  </section>
  <!-- /Popojok -->
</template>

<script>
import { baseUrl } from '@/config'
import { getListPopojok } from '@/service'

import PopojokAudio from './popojoks/Audio'
import PopojokVideo from './popojoks/Video'
import PopojokQuote from './popojoks/Quote'

export default {
  name: 'popojok',
  components: {
      'popojok-audio' : PopojokAudio,
      'popojok-video' : PopojokVideo,
      'popojok-quote' : PopojokQuote
  },

  data () {
    return {
      isShow: true,
      loading: true,
      loadingActive: false,
      popojoks: null,
      busy: false,
      page: 1,
      dummies: ['movies', 'music', 'book', 'photo', 'record', 'video', 'status' ],
      popojok_type : ['media_music','media_movie','media_book','recording','video','photo','quote','photo_text','recording_text','video_text','text','dialog']
    }
  },

  mounted(){

  },

  methods:{
    loadMore(){
      console.log('run')

      this.busy = true

      getListPopojok( this.page + 1 )
      .then(data => {

        if(data.data.data.length != 0)
        {
            setTimeout(() => {
              this.page++
              this.popojoks = [...this.popojoks, ...data.data.data]
              this.loading  = true
              this.busy     = false
            }, 1000)
        } else{
          this.loading = false
          this.busy     = true
        }
      })
      .catch(err => {
        console.log(err)
      })

    }
  },
  beforeMount ()
  {
    getListPopojok()
    .then(data => {
      this.popojoks = data.data.data

    })
    .catch(err => {
      console.log(err)
    })
  }
}
</script>

<style lang="scss" scoped>
  @import '../resources/sass/variables.scss';


  .section-popojok {
    min-height: 720px;
    margin-top: -70px;

    .popojok-main {
      padding: 40px 0;
      font-size: 14px;

      .popojok-item {
        width: 600px;
        max-width: 100%;
        margin: 0 auto;
        margin-bottom: 20px;
        box-shadow: 2px 4px 6px rgba( black, 0.05 );

        .item {
          &-header {
            background: white;
            padding: 10px;
            height: 60px;

            .header {
              &-author {
                float: left;
                position: relative;
                width: calc( 100% - 60px );

                .author {
                  &-avatar {
                    float: left;
                    width: 40px;
                    height: 40px;
                    overflow: hidden;
                    border-radius: 50%;
                    background: #eee;
                  }

                  &-name,
                  &-time {
                    float: left;
                    display: block;
                    width: calc( 100% - 50px );
                    height: 20px;
                    line-height: 20px;
                    margin-left: 10px;
                  }

                  &-name {
                    font-weight: 700;
                    height: 25px;
                    line-height: 25px;
                  }

                  &-time {
                    font-size: 12px;
                    height: 15px;
                    line-height: 15px;
                  }
                }
              }

              &-favorites {
                float: right;
                width: 60px;
                height: 40px;
                line-height: 40px;
                text-align: right;

                .favorites {
                  &-count {
                    position: relative;
                    display: inline-block;

                    &:before {
                      content: '\f004';
                      font-family: FontAwesome;
                      color: darken( white, 15% );
                      font-size: 20px;
                      position: absolute;
                      top: 0;
                      right: calc( 100% + 5px );
                    }
                  }
                }
              }
            }
          }

          &-footer {
            background: white;
            padding: 10px;

            p:last-child {
              margin-bottom: 0;
            }
          }

          &-content {
            &.type-1 {
              background: white;

              img {
                width: 100%;
                height: auto;
              }
            }

            &.type-2 {

            }

            &.type-3 {
              video{
                width: 100%;
                display: block;
              }
            }

            &.type-4 {
              padding: 10px;
              background: white;

              p:last-child {
                margin-bottom: 0;
              }
            }
          }
        }


      }
    }
  }
</style>



