import Vue from 'vue'
import Router from 'vue-router'
import Hello from '../components/Hello'
import Main from '../components/Main'
import Popojok from '../components/Popojok'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'home',
      component: Main
    },
    {
      path: '/popojok',
      name: 'popojok',
      component: Popojok
    }
  ]
})



