<template>
  <div>
    <!-- Hero -->
    <section id="section-hero" class="section-hero">
      <div class="container">
        <div class="content">
          <div class="content-col-res visible-sm">
            <img class="phone-1 display" v-if="mobile_detect == 'iphone'" src="../assets/img/phone-1.png" alt="">
            <img class="phone-2 display" v-if="mobile_detect == 'not-iphone'" src="../assets/img/phone-2.png" alt="">
          </div>

          <div class="content-col-right hidden-sm hidden-xs">
            <img src="../assets/img/phone-1.png" alt="">
          </div>

          <div class="content-col-middle">
            <div class="headline">
              <div class="line-1">Not Just</div>
              <div class="line-2">On The</div>
              <div class="line-3">Radio</div>
            </div>

            <div class="image visible-xs">
              <img class="phone-1 display" v-if="mobile_detect == 'iphone'" src="../assets/img/phone-1.png" alt="">
              <img class="phone-2 display" v-if="mobile_detect == 'not-iphone'" src="../assets/img/phone-2.png" alt="">
            </div>

            <div class="download">
              <div class="label">Download Now</div> <br>
              <a :href="station.apps_links.android" target="_blank">
                <img src="../assets/img/google-play.png" alt="">
              </a>
              <a :href="station.apps_links.ios" target="_blank">
                <img src="../assets/img/app-store.svg" alt="">
              </a>
            </div>
          </div>

          <div class="content-col-left hidden-sm hidden-xs">
            <img src="../assets/img/phone-2.png" alt="">
          </div>
        </div>

        <div class="decoration"><img src="../assets/img/popo.png" alt=""></div>
      </div>
    </section>
    <!-- /Hero -->

    <!-- Content -->
    <section id="section-content" class="section-content">
      <div class="container">
        <h2>Disiarkan di <span>33</span> Radio Stations</h2>

        <div class="stations-list">
          <div class="row">
            <div class="col-sm-6 col-md-4" v-for="(item, index) in station.data">
              <div class="list-item">
                <h3>{{ item.city }}</h3>
                <ul>
                  <li v-for="(item2, index2) in item.radios">
                    <b>{{ item2.name }}</b> &nbsp;&nbsp;|&nbsp;&nbsp; {{ item2.wave }} <span>{{ item2.time }} WIB</span>
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
    <!-- /Content -->
  </div>
</template>

<script>
import MobileDetect from 'mobile-detect';

export default {
  name: 'main',
  data () {
    return {
      msg: 'Main Page',
      station: null,
      mobile_detect: 'not-iphone'
    }
  },
  beforeMount () {
    let a = require('../stations.json')
    this.station = a
  },

  methods: {
    mobiledetect() {
      let vm = this;
      let mob = new MobileDetect( window.navigator.userAgent );

      if ( mob.is('iPhone') || mob.is('iPad')  ) {
        vm.mobile_detect = 'iphone';
      }
      else {
        vm.mobile_detect = 'not-iphone';
      }
    }
  },

  mounted() {
    this.mobiledetect();
  }


}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">

</style>



