<template>
  <div id="app" v-bind:class="'page-' + $route.name">
    <popo-header></popo-header>
    <transition 
      name="fade"
    >
      <router-view></router-view>
    </transition>
    <popo-footer></popo-footer>
  </div>
</template>

<script>
import Header from './components/partial/Header';
import Footer from './components/partial/Footer';

import "bootstrap/dist/css/bootstrap.min.css";
import "font-awesome/css/font-awesome.min.css";

export default {
  name: 'app',
  components: {
    'popo-header': Header,
    'popo-footer': Footer
  }
}
</script>

<style lang="scss">
  @import './resources/sass/app.scss';
</style>



