<template>
  <div>
      <p v-for="(q, i) in quote">
        {{ q }}
      </p>
  </div>
</template>

<script>
export default {
  name: 'quote',
  props: ['text'],
  computed: {
    quote () {
      return this.text.split("\n").filter((item) => item != "" )
    }
  }
}
</script>

<style scoped>

</style>



