import axios from 'axios'
import { baseUrl }  from './config'

const getRequest  =  async (url, page = null) => {

    try{
        let getFromApi = await axios.get(baseUrl + url,{
            params: {
                page
            }
        })

        return getFromApi
    } catch(e)
    {
        console.log(e)
    }

}


export const getListPopojok = async (page = null) => {
    try{
        let popojok = await getRequest("api/web/popojok/list", page)
        
        return popojok;
    } catch(e)
    {
        console.log(e)
    }
}


