<template>
    <div :id="id" class="audio-wrapper">

        <audio class="music"  preload="true">
            <source :src="src">
        </audio>
        <div class="audioplayer" >
            <button class="pButton play" @click="play"></button>
            <div class="timeline" >
                    <div class="playhead"></div>
            </div>
            <div class="currenTime">
                    {{ currentTime }}
            </div>
        </div>

    </div>
</template>

<script>
export default {
  name: 'Audio',
  data () {
    return {
        currentTime: this.duration.toString()
    }
  },
  props: ['src','id','duration'],
  methods: {

    play(){
        var vm = this

        var id = $(`#${this.id}`)

        var music    = id.find('.music')[0]
        var duration = music.duration; // Duration of audio clip, calculated here for embedding purposes
        var pButton  = id.find('.pButton')[0]
        var playhead = id.find('.playhead')[0]
        var timeline = id.find('.timeline')[0]

        // timeline width adjusted for playhead
        var timelineWidth = timeline.offsetWidth - playhead.offsetWidth;


        // play button event listenter

            //Play and Pause
        function play() {
            // start music
            if (music.paused) {
                music.play();
                $(pButton).removeClass('play').addClass('pause')
            } else { // pause music
                music.pause();
                $(pButton).removeClass('pause').addClass('play')
            }
        }

        //play here
        play()


       function secondsToHms(d) {
            d = Number(d);

            var m = Math.floor(d % 3600 / 60);
            var s = Math.floor(d % 3600 % 60);

            return ('0' + m).slice(-2) + ":" + ('0' + s).slice(-2);
        }

        // timeupdate event listener
        music.addEventListener("timeupdate", timeUpdate, false);


        // returns click as decimal (.77) of the total timelineWidth
        function clickPercent(event) {
            return (event.clientX - getPosition(timeline)) / timelineWidth;
        }

        // Boolean value so that audio position is updated only when the playhead is released
        var onplayhead = false;


        // timeUpdate
        // Synchronizes playhead position with current point in audio
        function timeUpdate() {
            var playPercent = timelineWidth * (music.currentTime   / duration);

            let convertToMinute = Math.round(music.duration).toFixed(2) - Math.round(music.currentTime).toFixed(2)

            vm.currentTime = secondsToHms(convertToMinute)

            playhead.style.marginLeft = playPercent + "px";
            if (music.currentTime == duration) {

                $(pButton).removeClass('pause').addClass('play')
            }
        }



        // Gets audio file duration
        music.addEventListener("canplaythrough", function() {
            duration = music.duration;
        }, false);

        // getPosition
        // Returns elements left position relative to top-left of viewport
        function getPosition(el) {
            return el.getBoundingClientRect().left;
        }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

.audio-wrapper{
    background: white;
}

.currenTime{
    position: absolute;
    top: 50%;
    right: 10px;
    font-size: 12px;
    margin-top: 10px;
}

.audioplayer{
    height: 60px;
    width: 100%;
    position: relative;
}

.pButton{
    height:40px;
    width: 40px;
    border: none;
    float:left;
    outline:none;
    background: #ED3237;
    border: 2px solid #F2F2F2;
    border-radius: 30px;
    margin: 10px;
}

.play{
   background: url("../../assets/img/play.png") center no-repeat;
   background-size: 100% 100%;
}
.pause{
    background: url("../../assets/img/pause.png") center no-repeat;
    background-size: 100% 100%;
}

.timeline{
    width: calc( 100% - 70px );
    height: 3px;
    float: left;
    border-radius: 15px;
    background: #9B9B9B;
    margin: 28.5px 0;
}
.playhead{
    width: 18px;
    height: 18px;
    border-radius: 50%;
    margin-top: -7px;
    background: #ED3237;
    border: 5px solid #E1E1E1;
    box-sizing: border-box

}

</style>



