<template>
  <!-- Master Header -->
  <header id="master-header" class="master-header">
    <div class="container">

      <!-- Logo -->
      <div class="logo">
        <h1 class="sr-only">Popo</h1>
        <img src="../../assets/img/logo.png" alt="">
        <span class="decoration"></span>
      </div>

      <!-- Menu Toggle -->
      <div class="toggle" v-on:click="menuSlide" v-bind:class="menu_state">
        <span></span>
        <span></span>
        <span></span>
      </div>

      <!-- Menu -->
      <div class="menu" v-bind:class="menu_state">
        <ul>
          <li v-on:click="menuSlide( $event, 'close', 'home' )" v-bind:class="{ 'active': ($route.name == 'home') }"><router-link to="/">Home</router-link></li>
          <li v-on:click="menuSlide( $event, 'close', 'popojok' )" v-bind:class="{ 'active': ($route.name == 'popojok') }"><router-link to="/popojok">POPOjok</router-link></li>
        </ul>
      </div>

      <!-- Menu Layer -->
      <div class="menu-layer" v-bind:class="menu_state"></div>
    </div>
  </header>
  <!-- /Master Header -->
</template>

<script>
export default {
  name: 'header',

  data() {
    let data = {
      menu_state: null,
      menu_active_route: this.$route.name
    };

    return data;
  },

  methods: {
    menuSlide( $event, method, route ) {
      let vm = this;

      let open = function() {
        vm.menu_state = 'open';
        vm.preventScroll('enable');
      };

      let close = function() {
        vm.menu_state = null;
        vm.preventScroll('disable');
      };

      if ( method ) {
        if ( method == 'close' ) {
          close();
        }
        else if ( method == 'open' ) {
          open();
        }
      }
      else {
        if ( vm.menu_state == null ) {
          open();
        }
        else {
          close();
        }
      }
    },

    preventScroll( method ) {
      let keys = {37: 1, 38: 1, 39: 1, 40: 1};

      let preventDefault = function(e) {
        e = e || window.event;
        if (e.preventDefault) e.preventDefault();
        e.returnValue = false;
      };

      let preventDefaultForScrollKeys = function(e) {
        if (keys[e.keyCode]) {
          preventDefault(e);
          return false;
        }
      }

      if ( method == 'enable' ) {
        window.onwheel = preventDefault;
        window.onmousewheel = document.onmousewheel = preventDefault;
        window.ontouchmove = preventDefault;
        document.onkeydown  = preventDefaultForScrollKeys;
      }

      if ( method == 'disable' ) {
        if (window.removeEventListener) window.removeEventListener('DOMMouseScroll', preventDefault, false);
        window.onmousewheel = document.onmousewheel = null;
        window.onwheel = null;
        window.ontouchmove = null;
        document.onkeydown = null;
      }
    }
  }
}
</script>

<style lang="scss" scoped>
  @import '../../resources/sass/variables.scss';

  .master-header {
    .menu {
      position: fixed;
      top: 0;
      left: 0;
      z-index: 11;
      width: 370px;
      height: 100%;
      background: lighten( $main-color, 2% );
      box-shadow: 4px 0 2px rgba( black, 0.05 );
      padding-top: 125px + 40px;
      transform: translate( -380px, 0 );
      transition: 0.4s;

      &.open {
        transform: translate( 0, 0 );
      }

      ul {
        padding: 0 40px;
        margin: 0;
        list-style: none;

        li {
          text-align: center;
          height: 40px;
          line-height: 40px;
          position: relative;
          cursor: pointer;

          &:before {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 2px;
            height: 100%;
            background: white;
          }

          &:after {
            content: '';
            display: block;
            position: absolute;
            top: 50%;
            left: 0;
            margin-left: -5px;
            margin-top: -6px;
            width: 12px;
            height: 12px;
            border: 2px solid lighten( $main-color, 2% );
            border-radius: 50%;
            background: white;
            transition: 0.2s;
          }

          a {
            color: white;
            display: inline-block;
            width: 100%;
            height: 100%;

            &:active,
            &:focus {
              text-decoration: none;
            }
          }

          &.active,
          &:hover {
            font-weight: 700;

            &:after {
              transform: scale(1.5);
            }

            a {
              text-decoration: none;
            }
          }
        }
      }
    }

    .menu-layer {
      display: none;
      position: fixed;
      top: 0;
      left: 0;
      z-index: 10;
      width: 100%;
      height: 100%;
      background: rgba( black, 0.6 );

      &.open {
        display: block;
      }
    }

    .toggle {
      cursor: pointer;

      &.open {
        span {
          &:first-child {
            opacity: 0;
          }

          &:nth-child(2) {
            transform: rotate(45deg) translate(3px, -3px);
            transform-origin: left;
          }

          &:nth-child(3) {
            transform: rotate(-45deg) translate(3px, 4px);
            transform-origin: left;
          }
        }
      }
    }
  }

  @media all and ( max-width: 1199px ) {
    .master-header {
      .menu {
        ul {
          padding: 0 70px;
        }
      }
    }
  }

  @media all and ( max-width: 992px ) {
    .master-header {
      .menu {
        ul {
          padding: 0 40px;
          margin-left: 55px;
        }
      }
    }
  }

  @media all and ( max-width: 767px ) {
    .master-header {
      .menu {
        width: 100%;
        transform: translate( calc( -100% - 10px ), 0 );

        ul {
          padding: 0 40px;
          margin-left: 0;

          li {
            &:before,
            &:after {
              display: none;
            }
          }
        }
      }

      .menu-layer {
        display: none !important;
      }
    }
  }
</style>



